import React, { Component } from 'react';
import officialAccount from './images/official-account.png';
import './style.css';

const OfficialAccount = ({ backgroundColor = '#F5F9FC' }) => (
  <div className="official-account" style={{ backgroundColor }}>
    <img src={officialAccount} />
    <span>每天推送精选技术文章</span>
  </div>
);

export default OfficialAccount;




