import React from 'react';
import Link from 'gatsby-link';

import Header from '../components/Header';
import NavMenu from '../components/NavMenu';
import Footer from '../components/Footer';
import OfficialAccount from '../components/OfficialAccount';
import './style.css';

class Template extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showNavMenu: false,
    };
  }

  renderContainer() {
    const { children } = this.props;
    if (this.state.showNavMenu) return false;
    return (
      <div className="layout-container">
        <div className="layout-content">
          <div className="layout-main">{children()}</div>
        </div>
        <div className="layout-official-account">
          <OfficialAccount />
        </div>
        <Footer />
      </div>
    );
  }

  render() {
    return (
      <div className="layout">
        <Header
          showNavMenu={() =>
            this.setState(pre => ({ showNavMenu: !pre.showNavMenu }))
          }
        >
          <NavMenu
            showNavMenu={this.state.showNavMenu}
            closeNavMenu={() => this.setState({ showNavMenu: false })}
          />
        </Header>
        {this.renderContainer()}
      </div>
    );
  }
}

export default Template;



