import React from 'react';
import { withRouter } from 'react-router-dom';
import './style.css';

const SourceNav = ({ history, showNavMenu, closeNavMenu }) => (
  <a
    onClick={() => {
      history.push('/');
      closeNavMenu();
    }}
  >
    首页
  </a>
);
const Nav = withRouter(SourceNav);

const NavMenu = ({ showNavMenu = false, closeNavMenu }) => {
  if (!showNavMenu) return false;
  return (
    <nav className="nav-menu">
      <Nav showNavMenu={showNavMenu} closeNavMenu={closeNavMenu} />
    </nav>
  );
};

export default NavMenu;



